export default {
  data() {
    return {
      isComment: 4,
      inputVal: ''
    };
  },
  methods: {
    // 输入内容后点击完成触发
    commentVal() {
      const userComment = this.inputVal; // 用户输入内容
      if (userComment == '') return;
      this.inputVal = '';
      this.$refs.tPopup.publish();
    }
  }
};
